<template>
	<div v-if="blog">

		<router-link to="/" type="button" class="Back-button">Back</router-link>
		<Appheader>
           <h1>{{blog.title}}</h1>
        </Appheader>

        <div class="blog-card-single">
        	<div class="blog-image">
        		<img :src="blog.image" alt="article title">
        	</div>
        	<h2 class="blog-title">{{blog.title}}</h2>
        	<p class="blog-content">{{blog.content}}</p>
        </div>
	</div>
</template>

<script>
import Appheader from '../components/app-header.vue';
import axios from 'axios';

export default{
	data(){
		return {
			blog:null
		}
	},
	mounted(){

		axios.get('https://raw.githubusercontent.com/mfekry10/simple_blog/master/db.json/blog/'+ this.$route.params.id)
		      .then((response) => {
		      	this.blog = response.data;
      }).catch((error) => {
    console.log(error.response);
})

		// console.log(this.$route.params.id);
	},
	components:{
		Appheader
	}
}
	
</script>

