<template>

	<div>
   <Appheader>
     <h1>Blog</h1>
   </Appheader>
    <div class="container" v-if="blogs">
      <appcard v-for="blog in blogs" :key="blog.id" :data="blog"></appcard>
    </div>

    <div class="text-center">
       <button 

       type="button" 
       class="load-more-button" 
       @click="loadmore()"
       v-if="blogs && blogs.length < totalcount">

       Load more</button>
    </div> 
  </div>

	
</template>



<script>


import Appheader from '../components/app-header.vue' 
import Appcard from '../components/app-card.vue' 
import axios from 'axios'


	export default {
  data() {

    return {
      blogs : null,
      page:1,
      totalcount:null
    }

  },

  components:{
    Appheader,
    Appcard
  },
  mounted () {
       this.getBlogs(this.page);
  },
  methods:{

    getBlogs(page){
      axios
      .get(`https://raw.githubusercontent.com/mfekry10/simple_blog/master/db.json?_page=${page}&_limit=6`)
      .then((response) => {

        // console.log(response.data);
         this.blogs = this.blogs ? this.blogs.concat(response.data) : response.data ;

         this.totalcount = response.headers['x-total-count'];
         console.log(response.headers['x-total-count']);
      }).catch((error) => {
    console.log(error.response);
      })
    },
    loadmore(){
      this.page++;
      this.getBlogs(this.page);
    }
  }
  
}
</script>

<style>

.load-more-button{
  color:#fff;
  background-color:#990099;
  border-radius:5px;
  border:none;
  padding:10px 25px;
  font-size:18px;
  line-height:1;
  margin-top:50px;
  margin-bottom:50px;
  outline: none;

}
	
</style>